<template>
  <div style="overflow: hidden;">
      <!-- Top Bar -->
      <div class="row ">
        <div class="col-12 top-bar text-center">
          <p>Capturing Moves from A to B</p>
        </div>
      </div>

      <div class="row">
        <div class="col-12 site-title text-center">
          <p class="site-title"><router-link to="/">Tracing Delta</router-link></p>
        </div>
      </div>
      
      <!-- Logged Out Application -->
      <div v-if="authed == false">
        <nav id="nav" class="td-nav">
          <ul class="td-nav-list">
            <li class="td-nav-link" v-on:click="toggleNav">
              <router-link to="/">Home</router-link>
            </li>
            <li class="td-nav-link" v-on:click="toggleNav">
              <router-link to="/blog">Blog</router-link>
            </li>
            <li class="td-nav-link" v-on:click="toggleNav">
              <router-link to="/contact">Contact</router-link>
            </li>
            <li class="td-nav-link" v-on:click="toggleNav">
              <router-link to="/sign-in">Sign In</router-link>
            </li>
          </ul>
          <a href="javascript:void(0);" class="icon" v-on:click="toggleNav">
            <i class="fa fa-bars"></i>
          </a>
        </nav>
        <!-- Ticker -->
        <Ticker />
        

        <router-view/>
      </div>

      <!-- Logged In Application -->
      <div v-if="authed == true">
        <nav id="nav" class="td-nav">
          <ul class="td-nav-list">
            <li class="td-nav-link" v-on:click="toggleNav">
              <router-link to="/app/dashboard">Dashboard</router-link>
            </li>
            <li class="td-nav-link" v-on:click="toggleNav">
              <router-link to="/app/technical-data">Technical Data</router-link>
            </li>
            <li class="td-nav-link" v-on:click="toggleNav">
              <router-link to="/app/my-account">My Account</router-link>
            </li>
            <li class="td-nav-link" v-on:click="toggleNav">
              <a href="javascript:void()" v-on:click="logOut">Log Out</a>
            </li>
          </ul>
          <a href="javascript:void(0);" class="icon" v-on:click="toggleNav">
            <i class="fa fa-bars"></i>
          </a>
        </nav>
        <!-- Ticker -->
        <Ticker />
        <router-view/>
      </div>

  </div>
</template>

<script>
import Ticker from './components/Ticker.vue';
import axios from 'axios'
const base_url = window.location.origin == "http://localhost:8080" ? "http://localhost:5000" : "https://www.tracingdelta.com"

export default {
  name: 'App',
  data () {
    return {
      authed: "null",
      paid_subscription: "null",
      subscription_active: "null",
      id: "null",
      firstname: "null",
      lastname: "null",
      email: "null"
    }
  },
  beforeCreate: function () {
    CheckAuth().then((response) => {
      if (response.data.success == false) {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = "null"
        this.subscription_active = "null"
        this.id = "null"
        this.firstname = "null"
        this.lastname = "null"
        this.email = "null"
        if (window.location.href.indexOf('/app/') > 0) {
          window.location.href = "/sign-in"
        }
      } else {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = response.data.user.paid_subscription
        this.subscription_active = response.data.user.subscription_active
        this.id = response.data.user._id
        this.firstname = response.data.user.firstname
        this.lastname = response.data.user.lastname
        this.email = response.data.user.email
      }
    })
  },
  created: function () {
    CheckAuth().then((response) => {
      if (response.data.success == false) {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = "null"
        this.subscription_active = "null"
        this.id = "null"
        this.firstname = "null"
        this.lastname = "null"
        this.email = "null"
        if (window.location.href.indexOf('/app/') > 0) {
          window.location.href = "/sign-in"
        }
      } else {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = response.data.user.paid_subscription
        this.subscription_active = response.data.user.subscription_active
        this.id = response.data.user._id
        this.firstname = response.data.user.firstname
        this.lastname = response.data.user.lastname
        this.email = response.data.user.email
      }
    })
  },
  beforeMount: function () {
    CheckAuth().then((response) => {
      if (response.data.success == false) {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = "null"
        this.subscription_active = "null"
        this.id = "null"
        this.firstname = "null"
        this.lastname = "null"
        this.email = "null"
        if (window.location.href.indexOf('/app/') > 0) {
          window.location.href = "/sign-in"
        }
      } else {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = response.data.user.paid_subscription
        this.subscription_active = response.data.user.subscription_active
        this.id = response.data.user._id
        this.firstname = response.data.user.firstname
        this.lastname = response.data.user.lastname
        this.email = response.data.user.email
      }
    })
  },
  mounted: function () {
    CheckAuth().then((response) => {
      if (response.data.success == false) {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = "null"
        this.subscription_active = "null"
        this.id = "null"
        this.firstname = "null"
        this.lastname = "null"
        this.email = "null"
        if (window.location.href.indexOf('/app/') > 0) {
          window.location.href = "/sign-in"
        }
      } else {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = response.data.user.paid_subscription
        this.subscription_active = response.data.user.subscription_active
        this.id = response.data.user._id
        this.firstname = response.data.user.firstname
        this.lastname = response.data.user.lastname
        this.email = response.data.user.email
      }
    })
  },
  updated: function () {
    CheckAuth().then((response) => {
      if (response.data.success == false) {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = "null"
        this.subscription_active = "null"
        this.id = "null"
        this.firstname = "null"
        this.lastname = "null"
        this.email = "null"
        if (window.location.href.indexOf('/app/') > 0) {
          window.location.href = "/sign-in"
        }
      } else {
        // Set session details
        this.authed = response.data.success
        this.paid_subscription = response.data.user.paid_subscription
        this.subscription_active = response.data.user.subscription_active
        this.id = response.data.user._id
        this.firstname = response.data.user.firstname
        this.lastname = response.data.user.lastname
        this.email = response.data.user.email
      }
    })
  },
  methods: {
    toggleNav: function () {
      var x = document.getElementById("nav")
      if (window.innerWidth <= 600) {
        if (x.className === "td-nav") {
          x.className += " responsive"
        } else {
          x.className = "td-nav"
        }
      }
      
    },
    logOut: function () {
      localStorage.setItem('token', "")
      window.location.href = "/sign-in"
    }
  },
  components: {
    Ticker
  }
}

function CheckAuth (to) {
  return axios.post(base_url + '/api/user/check-auth', {}, {
    headers: {'Authorization': "bearer " + localStorage.getItem('token')}
  })
  .then(function (response, to) {
    return response
  })
  .catch(function (error) {
    console.log(error);
  });
}

</script>

<style>
  body {
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 400;
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 700;
  }

  .hide {
    display: none !important;
  }

  .show {
    display: block !important;
  }

  .margin-center {
    display: block !important;
    margin: auto !important;
  }

  .top-bar {
    background: #000;
    color: #fff;
  }

  .top-bar > p {
    margin: 0px;
    padding: 6px;
  }

  .site-title > p {
    margin: 0px;
    padding: 16px;
    font-size: 36px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 700;
  }

  .site-title > p > a, .site-title > p > a:hover, .site-title > p > a:active, .site-title > p > a::after, .site-title > p > a:focus {
    color: #000;
    text-decoration: none;
  }

  .td-nav {

  }

  .td-nav-list {
    padding: 0px;
    text-align: center;
  }

  .td-nav-link {
    display: inline-block;
    padding: 4px 8px 4px 8px;
  }

  .td-nav-link > a {
    color: #000;
    font-weight: 300;
  }

  .ticker-container {
    border: 1px solid #000;
    margin-top: -16px;
    margin-bottom: 32px;
    overflow: hidden;
    padding: 0px;
  }

  .ticker-list {
    display: inline-block;
    white-space: nowrap;
    animation: marquee 30s linear infinite;
    width: auto;
    height: 24px;
  }

  .ticker-container:hover .ticker-list {
    animation-play-state: paused;
  }

  .ticker-item {
    margin-right: 20px;
    height: 16px;
  }

  .td-nav .icon {
    display: none;
    position: absolute;
    top: 64px;
    right: 48px;
    color: #000;
    padding: 4px 16px 4px 16px;
    border: 1px solid #000;
  } 
  
  .td-nav .icon:visited, .td-nav .icon:after, .td-nav .icon::after {
    color: #000;
  }

  @media screen and (max-width: 600px) {
    .site-title {
      text-align: left;
    }
    .td-nav li {display: none;}
    .td-nav a.icon {
      float: right;
      display: block;
      transition: 0.1s;
    }
    .td-nav.responsive {
      position: relative;
      padding: 16px;
      padding-bottom: 0px;
      background: #000;
    }
    .td-nav.responsive li {
      float: none;
      display: block;
      text-align: left;
      border-bottom: 1px solid white;
    }
    .td-nav.responsive li > a{
      color: #fff;
      font-family: 'IBM Plex sans';
    }
    .td-nav.responsive .td-nav-list > li:last-of-type {
      padding-bottom: 16px;
      border-bottom: none;
    }
    .td-nav.responsive > .icon {
      color:  #fff;
      background: #000;
      position: fixed;
    }
    .ticker-container {
      border-left: none;
      border-right: none;
    }
  }

  @keyframes marquee {
    from {
      transform: translateX(100%);
    }
    to {
      transform: translateX(-100%);
    }
  }

  @keyframes marquee2 {
    from {
      transform: translateX(0%);
    }
    to {
      transform: translateX(-200%);
    }
  }

  /* Buttons */
.btn {
  border: 1px solid #000;
  border-radius: 0px;
  font-weight: 700;
  font-size: 16px !important;
  cursor: pointer;
  display: inline-block;
  padding: 0 16px;
  font-size: 14px;
  line-height: 30px;
  text-decoration: none;
  vertical-align: middle;
  color: #000;
}
.btn:hover {
  background: #000;
  color: #fff;
}
.btn-primary {
  color: #fff;
  background: #000;
}

.btn-primary:hover {
  color: #000;
  background: #fff;
  border-color: #000;
}

.btn-secondary {

}

.btn-lg {
  font-size: 20px !important;
  padding: 16px 64px;
}
.btn-block+.btn-block {
  margin-top: 0px !important;
}
/* Inputs */
.form-control {
  margin-bottom: 8px;
  box-sizing: border-box !important;
  height: 40px !important;
  padding: 10px 12px !important;
  border: 1px solid black !important;
  border-radius: 0px !important;
  background-color: white !important;
  -webkit-box-shadow: 0 1px 3px 0 #e6ebf1 !important;
  box-shadow: 0 1px 3px 0 #e6ebf1 !important;
  -webkit-transition: box-shadow 150ms ease !important;
  -webkit-transition: -webkit-box-shadow 150ms ease !important;
  transition: -webkit-box-shadow 150ms ease !important;
  transition: box-shadow 150ms ease !important;
  transition: box-shadow 150ms ease, -webkit-box-shadow 150ms ease !important;
  font-size: '16px';
}

.form-control:focus {
  box-shadow: 0 1px 3px 0 #cfd7df !important;
}

textarea.form-control {
  height: auto !important;
}

.radio-option-container {
  margin: 0px;
  padding: 12px;
  border: 1px solid black;
}

.radio-option-container:first-of-type {
  border-bottom: none;
}

.radio-option-container:last-of-type {
  border-top: none;
}

.radio-option-container > label {
  margin: 0px;
}

.radio-option-container > input {

}

.radio-option-container input[type="radio"]:checked+label {
  font-weight: 700;
  color: #000;
}

/* Table Styles */
table, table thead, table tbody {
    width: 100%;
  }

  table {
    text-align: center;
    overflow: auto;
    display: block;
  }

  table thead {
    font-weight: 700;
    color: #fff;
    background: #000;
    padding: 8px;
    border: 1px solid #000;
  }

  table tbody {
    border: 1px solid #000;
  }

  table td, table th {
    border: 1px solid #000;
  }

  table td {
    padding: 4px;
  }

  table th {
    padding: 8px;
    background: #000;
    color: #fff;
  }
</style>



