<template>
  <div class="container">
    <div class="row">

      <div v-for="data in currencies" class="col-12 col-sm-12 col-md-6">

        <div class="currency-container">
          <h3>{{data.currency}}</h3>
          <div class="professional-sentiment">
            <h5 class="text-center">Professional Sentiment</h5>
            <div class="sentiment-label-container">
              <div class="long-label">
                <p>
                  <strong>
                    {{(data.professional_sentiment[0].long_percentage * 100).toFixed(2)}}%
                    <br/>
                    Long
                  </strong>
                </p>
              </div>
              <div class="short-label">
                <p>
                  <strong>
                    {{(data.professional_sentiment[0].short_percentage * 100).toFixed(2)}}%
                    <br/>
                    Short
                  </strong>
                </p>
              </div>
            </div>
            <div class="sentiment-data-container">
              <div class="long-percentage" :style="{ width: (data.professional_sentiment[0].long_percentage*100) + '%' }"></div>
              <div class="short-percentage" :style="{ width: (data.professional_sentiment[0].short_percentage*100) + '%' }"></div>
            </div>
          </div>
          <div class="retail-sentiment">
            <h5 class="text-center">Retail Sentiment</h5>
            <div class="sentiment-label-container">
              <div class="long-label">
                <p>
                  <strong>
                    {{(data.retail_sentiment.average_long_percentage * 100).toFixed(2)}}%
                    <br/>
                    Long
                  </strong>
                </p>
              </div>
              <div class="short-label">
                <p>
                  <strong>
                    {{(data.retail_sentiment.average_short_percentage * 100).toFixed(2)}}%
                    <br/>
                    Short
                  </strong>
                </p>
              </div>
            </div>
            <div class="sentiment-data-container">
              <div class="long-percentage" :style="{ width: (data.retail_sentiment.average_long_percentage * 100) + '%' }"></div>
              <div class="short-percentage" :style="{ width: (data.retail_sentiment.average_short_percentage * 100) + '%' }"></div>
            </div>
            <div class="retail-sentiment-details">
              <h6 class="text-center">Pair Break Down</h6>
              <p><button class="btn btn-block" v-on:click="toggleRetailPairs">Show Pairs</button></p>
              <div class="retail-sentiment-details-content hide">
                <div v-for="pair in data.retail_sentiment.sentiment_data">
                  <div class="sentiment-label-container">
                    <div class="long-label">
                      <p>
                        <strong>
                          {{(pair.data.long_percentage * 100).toFixed(2)}}%
                          <br/>
                          Long
                        </strong>
                      </p>
                    </div>
                    <div class="pair-label">
                      <p class="text-center">
                        <strong>
                        {{pair.pair.replace("_","/")}}
                        </strong>
                      </p>
                    </div>
                    <div class="short-label">
                      <p>
                        <strong>
                          {{(pair.data.short_percentage  * 100).toFixed(2)}}%
                          <br/>
                          Short
                        </strong>
                      </p>
                    </div>
                  </div>
                  <div class="sentiment-data-container">
                    <div class="long-percentage" :style="{ width: (pair.data.long_percentage * 100) + '%' }"></div>
                    <div class="short-percentage" :style="{ width: (pair.data.short_percentage  * 100) + '%' }"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="swing-data">
            <h5 class="text-center">Swing Distribution Statistics</h5>
            <br/>
            <div class="swing-data-container">
              <h6 class="text-center">Current Swing Data</h6>
              <table class="swing-data-table">
                <thead>
                  <tr>
                    <th>Pair</th>
                    <th>Direction</th>
                    <th>Run Up</th>
                    <th>Length</th>
                    <th>Extremes</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="pair in data.swing_data">
                    <td>{{pair.pair.replace("_","/")}} {{pair.time_frame == 1440 ? "(D)" : "(W)"}}</td>
                    <td>{{pair.current_swing_direction == 1 ? "Up" : "Down"}}</td>
                    <td>{{pair.current_swing_high_pips.toFixed(0)}}</td>
                    <td>{{pair.current_swing_length}}</td>
                    <td>{{pair.current_swing_high_breaks}}</td>
                  </tr>
                </tbody>
              </table>
              <br/>
              <h6 class="text-center">Probability of Swing Making a New Extreme X Times Before Retracing 200 Pips</h6>
              <table class="swing-data-table">
                <thead>
                  <tr>
                    <th>Pair</th>
                    <th>0</th>
                    <th>1</th>
                    <th>2</th>
                    <th>3</th>
                    <th>4</th>
                    <th>5</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="pair in data.swing_data">
                    <td>{{pair.pair.replace("_","/")}} {{pair.time_frame == 1440 ? "(D)" : "(W)"}}</td>
                    <td>{{(pair.no_breaks * 100).toFixed(2) + "%"}}</td>
                    <td>{{(pair.one_breaks * 100).toFixed(2) + "%"}}</td>
                    <td>{{(pair.two_breaks * 100).toFixed(2) + "%"}}</td>
                    <td>{{(pair.three_breaks * 100).toFixed(2) + "%"}}</td>
                    <td>{{(pair.four_breaks * 100).toFixed(2) + "%"}}</td>
                    <td>{{(pair.five_breaks * 100).toFixed(2) + "%"}}</td>
                  </tr>
                </tbody>
              </table>
              <br/>
              <h6 class="text-center">Probability of Swing Traveling X Pips</h6>
              <table class="swing-data-table">
                <thead>
                  <tr>
                    <th>Pair</th>
                    <th>50%</th>
                    <th>25%</th>
                    <th>12.5%</th>
                    <th>6.25%</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="pair in data.swing_data">
                    <td>{{pair.pair.replace("_","/")}} {{pair.time_frame == 1440 ? "(D)" : "(W)"}}</td>
                    <td>{{pair.run_up_cut_off_one}}</td>
                    <td>{{pair.run_up_cut_off_two}}</td>
                    <td>{{pair.run_up_cut_off_three}}</td>
                    <td>{{pair.run_up_cut_off_four}}</td>
                  </tr>
                </tbody>
              </table>
              <br/>
              <h6 class="text-center">Probability of Swing Lasting X Candles</h6>
              <table class="swing-data-table">
                <thead>
                  <tr>
                    <th>Pair</th>
                    <th>50%</th>
                    <th>25%</th>
                    <th>12.5%</th>
                    <th>6.25%</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="pair in data.swing_data">
                    <td>{{pair.pair.replace("_","/")}} {{pair.time_frame == 1440 ? "(D)" : "(W)"}}</td>
                    <td>{{pair.length_cut_off_one}}</td>
                    <td>{{pair.length_cut_off_two}}</td>
                    <td>{{pair.length_cut_off_three}}</td>
                    <td>{{pair.length_cut_off_four}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
import client_config from '../../client_config.js'
import client_helper from '../../client_helper.js'

export default {
  name: 'Dashboard',
  data () {
    return {
      currencies: [
        {
          currency: "EUR",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "USD",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "GBP",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "AUD",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "NZD",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "CAD",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "CHF",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "JPY",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        }
      ]
    }
  },
  beforeCreate: function () {
    client_helper.setSEOData("Dashboard - Tracing Delta", "Tracing Delta is chagning the game for retail traders by providing actionable stastics on the fundamental and technical aspects of the market.");
  },
  created: function () {
    if (this.$parent.paid_subscription == false) {
      this.currencies = [
        {
          currency: "EUR",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        },{
          currency: "USD",
          swing_data: [],
          retail_sentiment: [],
          professional_sentiment: []
        }
      ]
    }
    this.fetchCurrencyData()
  },
  methods: {
    fetchCurrencyData: async function () {
      let data = await axios.get(client_config.base_url + "/api/dashboard/get-all-data", {
        headers: {'Authorization': "bearer " + localStorage.getItem('token')}
      })
      .then(function (response) {
        if (response.data.success == false) {
          return false
        } else {
          return response.data.return_data
        }
      })
      .catch(function (error) {
        console.log(error);
      });

      // update currencies array with data
      this.currencies.forEach(currency => {
        let currency_code = currency.currency;
        let swing_data = data.swing_data.filter(function (d) {
          return d.pair.includes(currency_code)
        })
        let retail_sentiment = data.retail_sentiment.filter(function (d) {
          return d.pair.includes(currency_code)
        })
        let professional_sentiment = data.professional_sentiment.filter(function (d) {
          return d.currency.includes(currency_code)
        })

        // Swing Data
        currency.swing_data = swing_data
        // Retail Sentiment
        // Calculate Average Across All Pairs
        let long_sum = 0
        let short_sum = 0
        retail_sentiment.forEach(item => {
          if (item.pair.split('_')[0].includes(currency_code)) {
            long_sum += item.data.long_percentage
            short_sum += item.data.short_percentage
          } else {
            long_sum += item.data.short_percentage
            short_sum += item.data.long_percentage
          }
        })
        let retail_sentiment_data = {
          average_long_percentage: long_sum / retail_sentiment.length,
          average_short_percentage: short_sum / retail_sentiment.length,
          sentiment_data: retail_sentiment
        }

        currency.retail_sentiment = retail_sentiment_data
        // Professional Sentiment
        currency.professional_sentiment = professional_sentiment
      });
    },
    toggleRetailPairs: function (event) {
      let el = event.target;
      let retail_pairs_box = el.parentNode.parentNode.querySelector(".retail-sentiment-details-content")
      console.log(retail_pairs_box.classList)
      if (retail_pairs_box.classList.length > 1) {
        el.innerHTML = "Hide Pairs"
      } else {
        el.innerHTML = "Show Pairs"
      }
      retail_pairs_box.classList.toggle("hide")
    }
  },
}
</script>

<style scoped>
  .currency-container {
    border: 1px solid #000;
    padding: 16px;
    margin-bottom: 32px;
  }

  .professional-sentiment, .retail-sentiment, .swing-data, .economic-data {
    border: 1px solid #000;
    padding: 32px;
    margin-bottom: 32px;
  }

  .sentiment-data-container {
    width: 100%;
    height: 25px;
    border: 1px solid #000;
    margin-bottom: 16px;
  }
  .sentiment-data-container .long-percentage {
    width: 50%;
    background: #000;
    height: 23px;
    display: inline-block;
  }
  .sentiment-data-container .short-percentage {
    width: 50%;
    background: #fff;
    height: 23px;
    display: inline-block;
  }

  .sentiment-label-container {
    display: flex;
    width: 100%;
  }
  .sentiment-label-container .long-label {
    width: 50%;
    display: inline-block;
    float: left;
  }
  .sentiment-label-container .short-label {
    width: 50%;
    display: inline-block;
    float: right;
  }

  .sentiment-label-container .long-label p, .sentiment-label-container .short-label p  {
    text-align: center;
    width: 50px;
  }
  .sentiment-label-container .short-label p {
    float: right;
  }

  .swing-data-table, .swing-data-table thead, .swing-data-table tbody {
    width: 100%;
  }

  .swing-data-table {
    text-align: center;
    display: table !important;
  }

  .swing-data-table thead {
    font-weight: 700;
    color: #fff;
    background: #000;
    padding: 8px;
    border: 1px solid #000;
  }

  .swing-data-table tbody {
    font-size: 12px;
    border: 1px solid #000;
  }

  .swing-data-table td, .swing-data-table th {
    border: 1px solid #000;
  }

  .swing-data-table td {
    padding: 4px;
  }

  .swing-data-table th {
    padding: 8px;
    font-size: 12px;
  }
</style>



