<template>
  <div class="container">
    <div class="row blog-list-row">
        <div class="col-12 col-sm-6" v-for="post in posts">
            <div class="blog-post">
                <h3>{{post.title}}</h3>
                <div class="post-excerpt" v-html="post.excerpt">
                    {{post.excerpt}}
                </div>
                <div class="post-footer">
                    <router-link class="btn btn-block" :to="{ name: 'blog-single', params: {slug: post.URL } }">Read More</router-link>
                </div>
            </div>
        </div>
    </div>
    <br/>
    <div class="row pagination-row">
        <div class="col-6">
            <button id="previousPage" class="btn btn-block" style="display: none;" v-on:click="previousPage">Previous</button>
        </div>
        <div class="col-6">
            <button id="nextPage" class="btn btn-block" style="display: none;" v-on:click="nextPage">Next</button>
        </div>
    </div>
  </div>
</template>
<script>
    import axios from 'axios'
    import client_config from '../../client_config.js'
    import client_helper from '../../client_helper.js'

    export default {
        name: 'BlogList',
        data() {
            return {
            posts: {},
            total_posts: 0,
            posts_per_page: client_config.posts_per_page,
            page_index: 1
            }
        },
        beforeCreate: function () {
            client_helper.setSEOData("Blog - Tracing Delta", "Tracing Delta is chagning the game for retail traders by providing actionable stastics on the fundamental and technical aspects of the market.");
        },
        created() {
            this.togglePaginationButtons()
            this.fetchData()
        },
        updated() {
            this.togglePaginationButtons()
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData() {
                axios.get('https://public-api.wordpress.com/rest/v1.1/sites/tracingdeltablog.home.blog/posts/?number=' + this.posts_per_page + '&pretty=true&page=' + this.page_index)
                .then((resp) => {
                    resp.data.posts.forEach(post => {
                        let slug = post.URL.split("/")[post.URL.split("/").length - 2]
                        post.URL = slug
                    })
                    this.posts = resp.data.posts
                    this.total_posts = resp.data.found
                    this.posts_per_page = this.posts.length
                })
                .catch((err) => {
                    console.log(err)
                })
            },
            nextPage() {
                this.page_index = this.page_index + 1
                this.togglePaginationButtons()
                this.fetchData()
            },
            previousPage() {
                this.page_index = this.page_index - 1
                this.togglePaginationButtons()
                this.fetchData()
            },
            togglePaginationButtons() {
                if (this.page_index === 1) {
                    $('#previousPage').css('display', 'none')
                } else {
                    $('#previousPage').css('display', 'inline-block')
                }
                if (this.page_index === Math.floor(this.total_posts/this.posts_per_page)) {
                    $('#nextPage').css('display', 'none')
                } else {
                    $('#nextPage').css('display', 'inline-block')
                }
            }
        }
    }
</script>

<style scoped>
    .blog-list-row {
        padding: 16px;
    }
    .blog-post {
        max-width: 550px;
        margin: auto;
        border: 1px solid #000;
        padding: 32px 16px;
        margin-bottom: 32px;
        height: 297px;
        position:relative
    }

    .post-excerpt {
        height: 125px;
        overflow: hidden
    }
    
    .pagination-row {
        max-width: 360px;
        margin: auto;
    }

    .post-footer {
        position: absolute;
        bottom: 32px !important;
        left: 16px;
        width: calc(100% - 32px);
        max-width: 516px; 
    }
</style>


