                below), (pending verification that Tracing Delta has overriding
                legitimate grounds to process your personal data).
            </p>
        </li>
    </ul>
    <p dir="ltr">
        If you exercise your right to restrict processing, and if your exercise of
        said right is valid, then Tracing Delta can store your personal data but
        cannot otherwise process your personal data without your consent, unless
        otherwise processing your personal data is necessary for the establishment,
        exercise, or defense of legal claims, the protection of the rights of
        another natural or legal person, or for reasons of important public
        interest of the EU or a Member State.
    </p>
    <ol>
        <li dir="ltr">
            <p dir="ltr">
                Right to Object to Processing
            </p>
        </li>
    </ol>
    <h3 dir="ltr">
        Right to Object to Processing Based on Your Particular Situation
    </h3>
    <p dir="ltr">
        Where Tracing Delta’s processing of your personal information is necessary
        for legitimate interests pursued by Tracing Delta, you nevertheless have a
        right to object to Tracing Delta’s processing of your personal information
        on grounds relating to your particular situation. If you exercise this
        right, Tracing Delta cannot process your personal data unless Tracing Delta
        demonstrates that it has overriding legitimate grounds to process your
        personal data.
    </p>
    <h3 dir="ltr">
        Right to Object to Processing for Direct Marketing Purposes
    </h3>
    <p dir="ltr">
        You have a right to object to Tracing Delta’s processing of your personal
        information for direct marketing purposes. If you exercise this right,
        Tracing Delta cannot process your personal information for direct marketing
        purposes.
    </p>
    <ol>
        <li dir="ltr">
            <p dir="ltr">
                Right to Data Portability
            </p>
        </li>
    </ol>
    <p dir="ltr">
        Where Tracing Delta’s processing of your personal data is based on your
        consent or is necessary for the performance of a contract to which you are
        a party or is necessary to take steps requested by you prior to entering
        into a contract, and where Tracing Delta’s processing of your personal data
        is carried out by automated means, you have a right to data portability.
        This means that you have a right to receive from Tracing Delta a copy of
        the personal data you provided to Tracing Delta in a structured, commonly
        used, and machine-readable format. Additionally, this means that you have a
        right to require Tracing Delta to transmit directly to a third party the
        personal data you provided to Tracing Delta, provided that such
        transmission is technically feasible and provided further that such third
        party is a natural or legal person, public authority, agency, or other body
        which, alone or jointly with others, determines the purposes and means of
        processing personal data.
    </p>
    <ol>
        <li dir="ltr">
            <p dir="ltr">
                Right to Withdraw Consent to Processing
            </p>
        </li>
    </ol>
    <p dir="ltr">
        Where Tracing Delta’s processing of your personal data is based on your
        consent, you have a right to withdraw consent. However, your withdrawal of
        consent will not affect the lawfulness of Tracing Delta’s processing of
        your personal data prior to your withdrawal of consent.
    </p>
    <ol>
        <li dir="ltr">
            <p dir="ltr">
                Right to Lodge a Complaint with a Supervisory Authority
            </p>
        </li>
    </ol>
    <p dir="ltr">
        If you think that Tracing Delta’s processing of your personal data is
        unlawful or otherwise improper, you have a right to lodge a complaint with
        a Supervisory Authority. A “Supervisory Authority” is an independent public
        authority established by a Member State pursuant to Article 51 of the GDPR.
        You should contact the Supervisory Authority established by the Member
        State of which you are a citizen. If you cannot identify that Supervisory
        Authority, then you may contact Tracing Delta using the contact information
        provided in Paragraph 1, above. Tracing Delta will exercise its best
        efforts to assist you in identifying that Supervisory Authority.
    </p>
    <ol>
        <li dir="ltr">
            <p dir="ltr">
                Intention to Transmit Your Data to a Third Country
            </p>
        </li>
    </ol>
    <p dir="ltr">
        Tracing Delta is based in the United States. Information about you may be
        transferred to, and processed in, the United States and countries other
        than the country in which you are resident. These countries may have data
        protection laws that are different to the laws of your country and, in some
        cases, may not be as protective. Tracing Delta employs several safeguards
        to protect the transmission of your personal data. These safeguards include
        encryption of certain data and other commercially reasonable measures and
        practices. If you desire more information regarding these safeguards, then
        you should contact Tracing Delta using the contact information provided in
        Paragraph 1.
    </p>
    <p dir="ltr">
        Tracing Delta has not secured an Agency Decision from the European
        Commission that the safeguards it employs are adequate to protect your
        personal data.
    </p>
    <h1 dir="ltr">
        Miscellaneous
    </h1>
    <p dir="ltr">
        In addition to the Site, this policy also applies to any Tracing Delta
        mobile applications and any Information that we may collect through such
        applications.
    </p>
    <p dir="ltr">
        This Policy has been adopted and is effective as of the date set forth
        above, without waiving or otherwise releasing any right or obligation under
        any prior privacy policy or similar document or agreement of Tracing Delta,
        including without limitation any prior privacy policy or similar document
        or agreement set forth on the Site. This Policy hereby amends and restates
        any such prior privacy policy.
    </p>
    <p dir="ltr">
        Tracing Delta reserves the right to revise this Policy at any time without
        notice. By using the Site, you are agreeing to be bound by the then-current
        version of this Policy, and your continued use of the Site reaffirms your
        agreement to the then-current Policy. You agree to consult the Site
        regularly for up-to-date versions and information about this Policy.
        Without limiting the foregoing, we will provide notification by electronic
        mail, check box, highlighted text on the Site, or other means we deem
