        material revisions, amendments or other changes to the terms.
    </p>
    <p dir="ltr">
        In the event that the terms of this Policy conflict with or are
        inconsistent with any provision of the Terms and Conditions, the Terms and
        Conditions shall govern.
    </p>
    <p dir="ltr">
        Any claim under this Policy or otherwise related to the Site or Tracing
        Delta shall be governed by the laws of the State of Nevada without regard
