    </p>
    <p dir="ltr">
        The Site, and the other products and services of Tracing Delta, are solely
        directed to and intended for individuals residing in the United States.
        Those who choose to access and use the Site, and the other products and
        services of Tracing Delta, from other locations do so at their own risk,
