    </p>
    <p dir="ltr">
        Questions about this Policy should be addressed to team@TracingDelta.com.
    </p>
    <p dir="ltr">
        IF YOU DO NOT AGREE WITH ANY TERM OR PROVISION OF THIS POLICY, PLEASE EXIT
