<template>
    <!-- Ticker -->
    <div class="container">
        <div class="row">
            <div class="col-12 ticker-container">
              <div class="ticker-list">
                  <span v-for="data in ticker_data" class="ticker-item">
                    <strong>
                      {{data.pair.replace("_","")}}
                    </strong> 

                    <span class="ticker-item-current-price">
                      {{data.current_price}}
                    </span> 

                    <span v-if="data.candle_direction == 1" class="positive-on-day-caret">
                      <i class="fas fa-sort-up"></i>
                    </span>
                    <span v-if="data.candle_direction == 1" class="positive-on-day">
                      {{data.change.toFixed(5)}}
                    </span>
                    <span v-if="data.candle_direction == 1" class="positive-on-day">
                      +{{parseFloat(data.return*100).toFixed(2)}}%
                    </span>
                    
                    
                    <span v-if="data.candle_direction == 0" class="negative-on-day-caret">
                      <i class="fas fa-sort-down"></i>
                    </span>
                    <span v-if="data.candle_direction == 0" class="negative-on-day">
                      {{data.change.toFixed(5)}}
                    </span>
                    <span v-if="data.candle_direction == 0" class="negative-on-day">
                      {{parseFloat(data.return*100).toFixed(2)}}%
                    </span>
                    
                  </span>
              </div>

              <div class="ticker-list ticker-list-two">
                  <span v-for="data in ticker_data" class="ticker-item">
                    <strong>
                      {{data.pair.replace("_","")}}
                    </strong> 

                    <span class="ticker-item-current-price">
                      {{data.current_price}}
                    </span> 

                    <span v-if="data.candle_direction == 1" class="positive-on-day-caret">
                      <i class="fas fa-sort-up"></i>
                    </span>
                    <span v-if="data.candle_direction == 1" class="positive-on-day">
                      +{{data.change.toFixed(5)}}
                    </span>
                    <span v-if="data.candle_direction == 1" class="positive-on-day">
                      +{{parseFloat(data.return*100).toFixed(2)}}%
                    </span>
                    
                    
                    <span v-if="data.candle_direction == 0" class="negative-on-day-caret">
                      <i class="fas fa-sort-down"></i>
                    </span>
                    <span v-if="data.candle_direction == 0" class="negative-on-day">
                      {{data.change.toFixed(5)}}
                    </span>
                    <span v-if="data.candle_direction == 0" class="negative-on-day">
                      {{parseFloat(data.return*100).toFixed(2)}}%
                    </span>
                  </span>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import client_config from '../client_config.js'


export default {
  name: 'Ticker',
  data () {
    return {
      ticker_data: []
    }
  },
  created: function () {
    this.fetchData()
    let self = this
    setInterval(function () {
      self.fetchData()
    }, 60 * 1000)
  },
  methods: {
    fetchData: async function () {
      this.ticker_data = await axios.get(client_config.base_url + '/api/utilities/get-ticker-data')
      .then(function (response) {
        if (response.data.success == false) {
          return false
        } else {
          return response.data.ticker_data
        }
      })
      .catch(function (error) {
        console.log(error);
      });
    }
  }
}


</script>

<style>
  .ticker-container {
    border: 1px solid #000;
    margin-top: -16px;
    margin-bottom: 32px;
    overflow: hidden;
    padding: 0px;
  }

  .ticker-list {
    display: inline-block;
    white-space: nowrap;
    animation: marquee 15s linear infinite;
    animation-delay: -75s;
    -webkit-animation: marquee 150s linear infinite;
    -webkit-animation-delay: -75s;
    -moz-animation: marquee 150s linear infinite;
    -moz-animation-delay: -75s;
    -ms-animation: marquee 150s linear infinite;
    -ms-animation-delay: -75s;
    -o-animation: marquee 150s linear infinite;
    -o-animation-delay: -75s;
    width: auto;
    height: 25px;
    padding-left: 2px;
  }

  .ticker-list-two {
    animation-delay: 0s;
    -webkit-animation-delay: 0s;
    -moz-animation-delay: 0s;
    -ms-animation-delay: 0s;
    -o-animation-delay: 0s;
    position: absolute;
    top: 0px;
  }

  .ticker-container:hover .ticker-list {
    animation-play-state: paused;
  }

  .ticker-item {
    margin-right: 20px;
    height: 16px;
    width: 50px;
  }

  .ticker-item-current-price {
    font-size: 14px;
  }

  .positive-on-day {
    padding: 0px 2px;
    background: #9cf4dc;
    font-size: 14px;
  }
  .positive-on-day-caret {
    font-size: 16px;
  }
  .positive-on-day-caret > i {
    position: relative;
    top: 3px;
    color: #1ecd93;
  }

  .negative-on-day {
    padding: 0px 2px;
    background: #ffe1e1;
    font-size: 14px;
  }

  .negative-on-day-caret {
    font-size: 16px;
  }

  .negative-on-day-caret > i {
    position: relative;
    top: -3px;
    color: #ff433d;
  }


  @media screen and (max-width: 600px) {
    .ticker-container {
      border-left: none;
      border-right: none;
    }

    .ticker-list {
      animation: marquee 250s linear infinite;
      animation-delay: -125s;
      -webkit-animation: marquee 250s linear infinite;
      -webkit-animation-delay: -125s;
      -moz-animation: marquee 250s linear infinite;
      -moz-animation-delay: -125s;
      -ms-animation: marquee 250s linear infinite;
      -ms-animation-delay: -125s;
      -o-animation: marquee 250s linear infinite;
      -o-animation-delay: -125s;
    }

    .ticker-list-two {
      animation-delay: 0s;
      -webkit-animation-delay: 0s;
      -moz-animation-delay: 0s;
      -ms-animation-delay: 0s;
      -o-animation-delay: 0s;
    }
  }

  @keyframes marquee {
    0% {
      transform: translate(100%, 0);
    }
    100% {
      transform: translate(-100%, 0);
    }
  }
</style>



