<template>
  <div class="container">
    <div class="row">
      <div class="col-12 col-md-6">
        <div id="account-info" class="account-info-box">
          <h3>Account Info</h3>
          <span id="account-info-errors" class="main-validator"></span>
          <div class="account-info-container">
            <br/>
            <div class="row">
              <div class="col-6">
                <input class="form-control" type="text" id="firstname" placeholder="First Name" v-bind:value="this.firstname"/>
                <span id="firstname_validator" class="validator"></span>
              </div>
              <div class="col-6">
                <input class="form-control" type="text" id="lastname" placeholder="Last Name" v-bind:value="this.lastname"/>
                <span id="lastname_validator" class="validator"></span>
              </div>
              <div class="col-12">
                <input class="form-control" type="email" id="email" placeholder="Email" v-bind:value="this.email"/>
                <span id="email_validator" class="validator"></span>
              </div>
              <div class="change-password-inputs hide">
                <div class="col-12">
                  <input class="form-control" type="password" id="password" placeholder="New Password"/>
                  <span id="password_validator" class="validator"></span>
                </div>
                <div class="col-12">
                  <input class="form-control" type="password" id="confirm_password" placeholder="Confrim Password"/>
                  <span id="confirm_password_validator" class="validator"></span>
                </div>
              </div>
              <div class="change-password-btn">
                <div class="col-12">
                  <p><button class="btn btn-sm btn-block" v-on:click="showPasswordInputs">Change Password</button></p>
                </div>
              </div>
              <div class="col-12">
                <br/>
                <button class="btn btn-block btn-primary" v-on:click="updateAccountInfo">Submit</button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-12 col-md-6">
        <div id="subscription-info" class="account-info-box">
          <h3>Manage Subscription</h3>
          <span id="subscription-info-errors" class="main-validator"></span>
          <!-- Paid Subscriptions -->
          <div v-if="this.$parent.paid_subscription == true" class="subscription-info-container">
            <br/>
            <div class="row">
              <div class="update-payment-method-btn">
                <div class="col-12">
                    <p><button class="btn btn-sm btn-block btn-primary" v-on:click="showPaymentMethodInputs">Update Payment Method</button></p>
                </div>
              </div>
              <div class="update-payment-method-inputs hide">
                <div class="col-12">
                  <h4>Payment Info</h4>
                  <div id="payment-form">
                    <div class="form-row">
                      <label for="card-element">
                        Credit or Debit Card
                      </label>
                      <div id="card-element">
                        <!-- A Stripe Element will be inserted here. -->
                      </div>

                      <!-- Used to display Element errors. -->
                      <div id="card-errors" role="alert"></div>
                    </div>
                    <div class="row">
                      <div class="col-6">
                        <button class="btn btn-sm btn-block" v-on:click="showPaymentMethodInputs">Cancel</button>
                      </div>
                      <div class="col-6">
                        <button class="btn btn-sm btn-block btn-primary" v-on:click="changePaymentMethod">Submit</button>
                      </div>
                    </div>
                  </div>
                  <br/>
                </div>
              </div>
              <br/>
              <div class="modify-subscription-btn">
                <!-- Active Subscriptions -->
                <div class="col-12" v-if="this.$parent.subscription_active == true" >
                    <p><button class="btn btn-sm btn-block btn-primary" v-on:click="showCancelSubInputs">Cancel Subscription</button></p>
                </div>
                <!-- Cancled Subscriptions
                <div class="col-12" v-if="this.$parent.subscription_active == false" >
                    <p><button class="btn btn-sm btn-block" v-on:click="showActivateSubInputs">Activate Subscription</button></p>
                </div>
                 -->
              </div>
              <div class="modify-subscription-inputs hide">
                <!-- Active Subscriptions -->
                <div class="col-12" v-if="this.$parent.subscription_active == true" >
                    <p>If you would like to cancel your subscription, please send us an email at <a href="mailto:sales@tracingdelta.com">sales@tracingdelta.com</a> requesting a cancellation with the email on the account.</p>
                    <p><button class="btn btn-sm btn-block" v-on:click="showCancelSubInputs">Close</button></p>
                </div>
                <!-- Cancled Subscriptions 
                <div class="col-12" v-if="this.$parent.subscription_active == false" >
                    <p></p>
                </div>
                -->
              </div>
            </div>
          </div>

          <!-- Free Subscriptions -->
          <div v-if="this.$parent.paid_subscription == false" class="subscription-info-container">
            <br/>
            <div class="row">
              <div class="col-12">
                <p>Upgrade your account today to receive access to data for all 8 major currencies and 28 currency pairs!</p>
              </div>
              <div class="upgrade-subscription-btn">
                <div class="col-12">
                    <p><button class="btn btn-sm btn-block btn-primary" v-on:click="showUpgradeSubInputs">Upgrade Subscription</button></p>
                </div>
              </div>

              <div class="upgrade-subscription-inputs hide">
                <div class="col-12">
                  <h4>Membership Plan</h4>
                  <div class="radio-option-container">
                    <input type="radio" id="monhtly" name="plan" value="monthly">
                    <label for="monhtly">Monthly ($299.95/month)</label>
                  </div>

                  <div class="radio-option-container">
                    <input type="radio" id="yearly" name="plan" value="yearly" checked>
                    <label for="yearly">Yearly ($2999.95/year) - Save 15%</label>
                  </div>
                  <br/>
                  <h4>Payment Info</h4>
                  <div id="payment-form-two">
                    <div class="form-row">
                      <label for="card-element">
                        Credit or Debit Card
                      </label>
                      <div id="card-element-two">
                        <!-- A Stripe Element will be inserted here. -->
                      </div>

                      <!-- Used to display Element errors. -->
                      <div id="card-errors-two" role="alert"></div>
                    </div>
                    <div class="row">
                      <div class="col-6">
                        <button class="btn btn-sm btn-block" v-on:click="showUpgradeSubInputs">Cancel</button>
                      </div>
                      <div class="col-6">
                        <button class="btn btn-block btn-primary" v-on:click="upgradeSubscription">Submit</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import client_config from '../../client_config.js'
import client_helper from '../../client_helper.js'

function validateAccountInfo() {
  document.getElementById("firstname_validator").innerHTML = ""
  document.getElementById("lastname_validator").innerHTML = ""
  document.getElementById("email_validator").innerHTML = ""
  document.getElementById("password_validator").innerHTML = ""
  document.getElementById("confirm_password_validator").innerHTML = ""

  // Validate inputs
  let errors = 0
  let firstname = document.getElementById("firstname").value;
  let lastname = document.getElementById("lastname").value;
  let email = document.getElementById("email").value;
  let password = document.getElementById("password").value;
  let confirm_password = document.getElementById("confirm_password").value;
  if (firstname == "") {
    document.getElementById("firstname_validator").innerHTML = "Required.";
    errors++
  }
  if (lastname == "") {
    document.getElementById("lastname_validator").innerHTML = "Required.";
    errors++
  }
  if (email == "") {
    document.getElementById("email_validator").innerHTML = "Required.";
    errors++
  } else {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (re.test(String(email).toLowerCase()) == false) {
        document.getElementById("email_validator").innerHTML = "Please enter a valid email.";
        errors++
      }
  }
  if (password != "" || confirm_password != "" ) {
    if (password == "") {
      document.getElementById("password_validator").innerHTML = "Required."
      errors++
    }
    if (confirm_password == "") {
      errors++
      document.getElementById("confirm_password_validator").innerHTML = "Required."
    }
    if (password != confirm_password) {
      errors++
      document.getElementById("confirm_password_validator").innerHTML = "Passwords do not match."
    }
  }
  if (errors == 0) {
    return true
  } else {
    return false
  }
}

export default {
  name: 'MyAccount',
  data () {
    return {
      firstname: this.$parent.firstname,
      lastname: this.$parent.lastname,
      email: this.$parent.email,
      password: "",
      new_password: "",
      stripe: "",
      card: "",
      update_payment_method_mounted: false
    }
  },
  beforeCreate: function () {
    client_helper.setSEOData("My Account - Tracing Delta", "Tracing Delta is chagning the game for retail traders by providing actionable stastics on the fundamental and technical aspects of the market.");
  },
  methods: {
    updateAccountInfo: async function () {
      let validate_inputs = validateAccountInfo()
      if (validate_inputs == false) return
      let firstname = document.getElementById('firstname').value;
      let lastname = document.getElementById('lastname').value;
      let email = document.getElementById('email').value;
      let password = document.getElementById('password').value;
      let confirm_password = document.getElementById('password').value;

      let updated = await axios.post(client_config.base_url + '/api/user/update', {
        firstname: firstname,
        lastname: lastname,
        email: email,
        password: password == "" && confirm_password == "" ? "" : password
      },
      {
        headers: {'Authorization': "bearer " + localStorage.getItem('token')}
      })
      .then(function (response) {
        document.getElementById("account-info-errors").innerHTML = response.data.message
        if (response.data.success == false) {
          return false
        } else {
          if (document.getElementsByClassName('change-password-inputs')[0].classList.contains("hide") == false) {
            document.getElementsByClassName('change-password-inputs')[0].classList.toggle('hide')
            document.getElementsByClassName('change-password-btn')[0].classList.toggle('hide')
          }
          return true
        }
      })
      .catch(function (error) {
        console.log(error);
      });
      
    },
    showPasswordInputs: function () {
      document.getElementsByClassName('change-password-inputs')[0].classList.toggle('hide')
      document.getElementsByClassName('change-password-btn')[0].classList.toggle('hide')
    },
    showPaymentMethodInputs: function () {
      if (this.update_payment_method_mounted == false) {
        // Create a Stripe client.
        var stripe = Stripe('pk_test_KppRrwaI7pgeM0jlynUoga5k00tSx8KjPn')
        this.stripe = stripe
      
        // Create an instance of Elements.
        var elements = stripe.elements();
        var style = {
          base: {
            color: '#32325d',
            fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
            fontSmoothing: 'antialiased',
            fontSize: '16px',
            '::placeholder': {
              color: '#aab7c4'
            }
          },
          invalid: {
            color: '#fa755a',
            iconColor: '#fa755a'
          }
        };

        // Create an instance of the card Element.
        var card = elements.create('card', {style: style});
        this.card = card;

        // Add an instance of the card Element into the `card-element` <div>.
        card.mount('#card-element');
        this.update_payment_method_mounted = true
      }
      
      // Change Views
      document.getElementsByClassName('update-payment-method-inputs')[0].classList.toggle('hide')
      document.getElementsByClassName('update-payment-method-btn')[0].classList.toggle('hide')
    },
    changePaymentMethod: async function () {
      const stripe = this.stripe
      // Create Stipe Token
      this.stripe_token = await stripe.createToken(this.card).then(function(result) {
        if (result.error) {
          // Inform the customer that there was an error.
          var errorElement = document.getElementById('card-errors');
          errorElement.textContent = result.error.message;
        } else {
          // Send the token to your server.
          return result.token
        }
      });

      // Send Data to Server
      let subscription_upgraded = await axios.post(client_config.base_url + '/api/user/update-payment-method', {
        stripe_token: this.stripe_token,
      },
      {
        headers: {'Authorization': "bearer " + localStorage.getItem('token')}
      })
      .then(function (response) {
        document.getElementById("subscription-info-errors").innerHTML = response.data.message
        if (response.data.success == false) {
          return false
        } else {
          return true
        }
      })
      .catch(function (error) {
        console.log(error);
      });
      
      if (subscription_upgraded == true) {
        // Change Views
        document.getElementsByClassName('update-payment-method-inputs')[0].classList.toggle('hide')
        document.getElementsByClassName('update-payment-method-btn')[0].classList.toggle('hide')
      }
    },
    showCancelSubInputs: function () {
      // Change Views
      document.getElementsByClassName('modify-subscription-inputs')[0].classList.toggle('hide')
      document.getElementsByClassName('modify-subscription-btn')[0].classList.toggle('hide')
    },
    cancelSubscription: function () {

    },
    showActivateSubInputs: function () {

    },
    activateSubscription: function () {

    },
    showUpgradeSubInputs: function () {
      if (this.update_payment_method_mounted == false) {
        // Create a Stripe client.
        var stripe = Stripe('pk_test_KppRrwaI7pgeM0jlynUoga5k00tSx8KjPn')
        this.stripe = stripe
      
        // Create an instance of Elements.
        var elements = stripe.elements();
        var style = {
          base: {
            color: '#32325d',
            fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
            fontSmoothing: 'antialiased',
            fontSize: '16px',
            '::placeholder': {
              color: '#aab7c4'
            }
          },
          invalid: {
            color: '#fa755a',
            iconColor: '#fa755a'
          }
        };

        // Create an instance of the card Element.
        var card = elements.create('card', {style: style});
        this.card = card;

        // Add an instance of the card Element into the `card-element` <div>.
        card.mount('#card-element-two');
        this.update_payment_method_mounted = true
      }
      
      // Change Views
      document.getElementsByClassName('upgrade-subscription-inputs')[0].classList.toggle('hide')
      document.getElementsByClassName('upgrade-subscription-btn')[0].classList.toggle('hide')
    },
    upgradeSubscription: async function () {
      var radios = document.getElementsByName('plan');
      for (var i = 0, length = radios.length; i < length; i++) {
        if (radios[i].checked){
          this.subscription_plan = radios[i].value;
          break;
        }
      }
      const stripe = this.stripe
      // Create Stipe Token
      this.stripe_token = await stripe.createToken(this.card).then(function(result) {
        if (result.error) {
          // Inform the customer that there was an error.
          var errorElement = document.getElementById('card-errors');
          errorElement.textContent = result.error.message;
        } else {
          // Send the token to your server.
          return result.token
        }
      });

      // Send Data to Server
      let subscription_upgraded = await axios.post(client_config.base_url + '/api/user/upgrade', {
        stripe_token: this.stripe_token,
        subscription_plan: this.subscription_plan
      },
      {
        headers: {'Authorization': "bearer " + localStorage.getItem('token')}
      })
      .then(function (response) {
        document.getElementById("subscription-info-errors").innerHTML = response.data.message
        if (response.data.success == false) {
          return false
        } else {
          return true
        }
      })
      .catch(function (error) {
        console.log(error);
      });
      
      if (subscription_upgraded == true) {
        // Change Views
        document.getElementsByClassName('upgrade-subscription-inputs')[0].classList.toggle('hide')
        document.getElementsByClassName('upgrade-subscription-btn')[0].classList.toggle('hide')
      }
    }
  },
}
</script>

<style scoped>
.account-info-box {
    padding: 16px;
    border: 1px solid black;
    width: 100%;
    margin-bottom: 32px;
  }
.change-password-inputs, .change-password-btn, .update-payment-method-btn, .update-payment-method-inputs, .modify-subscription-btn, .modify-subscription-inputs, .upgrade-subscription-btn, .upgrade-subscription-inputs {
  width: 100%;
}

#card-element, #card-element-two {
   width: 100% !important;
   margin: 16px 0px 16px 0px;
 }
.StripeElement {
  box-sizing: border-box !important;
  height: 40px !important;
  padding: 10px 12px !important;
  border: 1px solid black !important;
  border-radius: 0px !important;
  background-color: white !important;
  box-shadow: 0 1px 3px 0 #e6ebf1 !important;
  -webkit-transition: box-shadow 150ms ease !important;
  transition: box-shadow 150ms ease !important;
}

.StripeElement--focus {
  box-shadow: 0 1px 3px 0 #cfd7df !important;
}

.StripeElement--invalid {
  border-color: #fa755a !important;
}

.StripeElement--webkit-autofill {
  background-color: #fefde5 !important;}
#card-element, #card-element-two {
  margin: 0px 0px 32px 0px;
}
</style>



