<template>
  <div class="container">
    <div class="row checkout-row">
      <div class="col-12 col-sm-6">
        <h2 class="text-center">Join Today to Receive These Great Features</h2>
        <table class="features-table">
          <thead>
            <tr>
              <th class="table-col-one">Feature</th>
              <th class="table-col-two">Premium</th>
              <th class="table-col-three">Free</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Professional Sentiment Data for All 8 Major Currencies</td>
              <td class="text-center"><i class="fas fa-check"></i></td>
              <td class="text-center"><i class="fas fa-check"></i></td>
            </tr>
            <tr>
              <td>Retail Sentiment Data for All 28 Major Currency Pairs & Crosses</td>
              <td class="text-center"><i class="fas fa-check"></i></td>
              <td class="text-center"><i class="fas fa-check"></i></td>
            </tr>
            <tr>
              <td>Statistical Data & Analysis for EUR/USD (Daily Time Frame)</td>
              <td class="text-center"><i class="fas fa-check"></i></td>
              <td class="text-center"><i class="fas fa-check"></i></td>
            </tr>
            <tr>
              <td>Statistical Data & Analysis for All 27 Other Major Currency Pairs & Crosses (Daily Time Frame)</td>
              <td class="text-center"><i class="fas fa-check"></i></td>
              <td class="text-center"><i class="fas fa-times"></i></td>
            </tr>
            <tr>
              <td>Access to Our Real-time Chatroom</td>
              <td class="text-center"><i class="fas fa-check"></i></td>
              <td class="text-center"><i class="fas fa-times"></i></td>
            </tr>
          </tbody>
        </table>
        <br/>
      </div>
      <div class="col-12 col-sm-6">
        <div id="plan-info" class="checkout-box show">
          <h3>Membership Plan</h3>
          <div class="checkout-box-content">
            <div class="radio-option-container">
              <input type="radio" id="free" name="plan" value="free">
              <label for="free">Free</label>
            </div>

            <div class="radio-option-container">
              <input type="radio" id="monthly" name="plan" value="monthly">
              <label for="monthly">Monthly ($299.95/month)</label>
            </div>

            <div class="radio-option-container">
              <input type="radio" id="yearly" name="plan" value="yearly" checked>
              <label for="yearly">Yearly ($2999.95/year) - Save 15%</label>
            </div>
            <br/>
            <button class="btn btn-block btn-primary" v-on:click="moveToAccountInfo">Next</button>
          </div>
          
        </div>

        <div id="account-info" class="checkout-box">
          <h3>Account Info</h3>
          <span id="account-info-errors" class="main-validator"></span>
          <div class="checkout-box-content hide">
            <br/>
            <div class="row">
              <div class="col-6">
                <input class="form-control" type="text" id="firstname" placeholder="First Name"/>
                <span id="firstname_validator" class="validator"></span>
              </div>
              <div class="col-6">
                <input class="form-control" type="text" id="lastname" placeholder="Last Name"/>
                <span id="lastname_validator" class="validator"></span>
              </div>
              <div class="col-12">
                <input class="form-control" type="email" id="email" placeholder="Email"/>
                <span id="email_validator" class="validator"></span>
              </div>
              <div class="col-12">
                <input class="form-control" type="password" id="password" placeholder="Password"/>
                <span id="password_validator" class="validator"></span>
              </div>
              <div class="col-12">
                <br/>
                <div class="row">
                  <div class="col-6">
                    <button class="btn btn-block" v-on:click="moveBackToPlanInfo">Back</button>
                  </div>
                  <div class="col-6">
                    <button class="btn btn-block btn-primary" id="paid-next" v-on:click="moveToPaymentInfo">Next</button><button id="free-next" class="btn btn-block hide btn-primary" v-on:click="completeRegistration">Register</button>
                  </div>
                </div>
                
              </div>
            </div>
            
          </div>
        </div>

        <div id="payment-info" class="checkout-box">
          <h3>Payment Info</h3>
          <div class="checkout-box-content hide">
            <br/>
            <div id="payment-form">
              <div class="form-row">
                <label for="card-element">
                  Credit or Debit Card
                </label>
                <div id="card-element">
                  <!-- A Stripe Element will be inserted here. -->
                </div>

                <!-- Used to display Element errors. -->
                <div id="card-errors" role="alert"></div>
              </div>
              <div class="row">
                <div class="col-6">
                  <button class="btn btn-block" v-on:click="moveBackToAccountInfo">Back</button>
                </div>
                <div class="col-6">
                  <button class="btn btn-block btn-primary" v-on:click="completeRegistration">Submit</button>
                </div>
              </div>
            </div>
          </div>
          
        </div>
      </div>
    </div>
    <div class="row thank-you-row hide">
      <div class="col-12">
        <div class="thank-you-container">
          <h1 class="text-center">You're in!</h1>
          <p class="text-center">
            Thank you for joining Tracing Delta!
            <br/>
            Click the button below to go to your dashboard.
          </p>
          <p>
            <a class="btn margin-center" href="/app/dashboard">Visit My Dashboard</a>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import client_config from '../../client_config.js'
import client_helper from '../../client_helper.js'


function validateAccountInfo() {
  document.getElementById("firstname_validator").innerHTML = ""
  document.getElementById("lastname_validator").innerHTML = ""
  document.getElementById("email_validator").innerHTML = ""
  document.getElementById("password_validator").innerHTML = ""
  // Validate inputs
  let errors = 0
  let firstname = document.getElementById("firstname").value;
  let lastname = document.getElementById("lastname").value;
  let email = document.getElementById("email").value;
  let password = document.getElementById("password").value;
  if (firstname == "") {
    document.getElementById("firstname_validator").innerHTML = "Required.";
    errors++
  }
  if (lastname == "") {
    document.getElementById("lastname_validator").innerHTML = "Required.";
    errors++
  }
  if (email == "") {
    document.getElementById("email_validator").innerHTML = "Required.";
    errors++
  } else {
      var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (re.test(String(email).toLowerCase()) == false) {
        document.getElementById("email_validator").innerHTML = "Please enter a valid email.";
        errors++
      }
  }
  if (password == "") {
    document.getElementById("password_validator").innerHTML = "Required."
    errors++
  }
  if (errors == 0) {
    return true
  } else {
    return false
  }
}

function gtmGetProduct() {
  let plan
  var radios = document.getElementsByName('plan');
  for (var i = 0, length = radios.length; i < length; i++) {
    if (radios[i].checked){
      plan = radios[i].value;
      break;
    }
  }
  let name = "Subscription - " + plan
  let cost;
  if (plan == 'free') cost = 0.00;
  if (plan == 'monthly') cost = 299.95
  if (plan == 'subscription') cost = 2999.95
  let product = { 'name': name, 'id': plan, 'price': cost, 'category': "Subscription", "quantity": 1 };
  let products = [product]
  return products
}

function gtmCheckoutStepOne() {
  let gtm_step_one_complete = sessionStorage.getItem("gtmStepOne")
  if (gtm_step_one_complete != "true") {
    let products = gtmGetProduct()
    dataLayer.push({
      'event': 'checkout',
      'ecommerce': {
        'checkout': {
            'actionField': { 'step': 1 },
            'products': products
        }
      }
    });
    dataLayer.push({
      'event': 'fbCheckoutInitiated'
    });
    sessionStorage.setItem("gtmStepOne", "true")
  }
}

function gtmAddToCart() {
  let gtm_atc_complete = sessionStorage.getItem("gtmATC")
  if (gtm_atc_complete != "true") {
    let products = gtmGetProduct()
    // Make Add to Cart Calls
    dataLayer.push({
      "event": 'addToCart',
      "ecommerce": {
        "currencyCode": 'USD',
        "add": {
          "products": products
        }
      }
    });

    // Facebook Product Info
    var facebookProducts = [];
    var facebookContentIDs = [];
    var fb_product = {
        "id": products[0].id,
        "quantity": 1,
        "item_price": parseFloat(products[0].price)
    };
    facebookProducts.push(fb_product);
    facebookContentIDs.push(products[0].id);
    dataLayer.push({
      "event": "fbATC",
      "facebookContentIDs": facebookContentIDs,
      "facebookProducts": facebookProducts,
      "content_type": "product",
      "value": parseFloat(products[0].price)
    });
    sessionStorage.setItem("gtmATC", "true")
  }
  
}

function gtmCheckoutStepTwo() {
  let gtm_step_two_complete = sessionStorage.getItem("gtmStepTwo")
  if (gtm_step_two_complete != "true") {
    let products = gtmGetProduct()
    dataLayer.push({
      'event': 'checkout',
      'ecommerce': {
        'checkout': {
            'actionField': { 'step': 2 },
            'products': products
        }
      }
    });
    sessionStorage.setItem("gtmStepTwo", "true")
  } 
}

function gtmCheckoutStepThree() {
  let gtm_step_three_complete = sessionStorage.getItem("gtmStepThree")
  if (gtm_step_three_complete != "true") {
    let products = gtmGetProduct()
    dataLayer.push({
      'event': 'checkout',
      'ecommerce': {
        'checkout': {
            'actionField': { 'step': 3 },
            'products': products
        }
      }
    });
    sessionStorage.setItem("gtmStepThree", "true")

    // Facebook Add Payment Method
    dataLayer.push({
      'event': 'fbAddPaymentMethod',
    });
    
  }
}

function gtmCheckoutStepFour() {
  let gtm_step_four_complete = sessionStorage.getItem("gtmStepFour")
  if (gtm_step_four_complete != "true") {
    let products = gtmGetProduct()
    dataLayer.push({
      'event': 'checkout',
      'ecommerce': {
        'checkout': {
            'actionField': { 'step': 4 },
            'products': products
        }
      }
    });
    sessionStorage.setItem("gtmStepFour", "true")

    // Send Transaction Data
    dataLayer.push({
      'event': 'transactionComplete',
      'ecommerce': {
          'purchase': {
              'actionField': {
                  'id': document.getElementById("email").value,
                  'affiliation': 'Website',
                  'revenue': products[0].price,
                  'tax': '0.00',
                  'shipping': '0.00',
                  'coupon': ''
              },
              'products': products
          }
      },
      'transactionId': document.getElementById("email").value,
      'transactionTotal': products[0].price,
      'transactionTax': '0.00',
      'transactionProducts': products
    });

    // Facebook Event
    var facebookProducts = [];
    var facebookContentIDs = [];
    products.forEach(item => {
        var fb_product = {
            "id": item.id,
            "quantity": item.quantity,
            "item_price": parseFloat(item.price)
        };
        facebookProducts.push(fb_product);
        facebookContentIDs.push(item.id);
    });
    dataLayer.push({
        "event": "fbPurchaseComplete",
        "facebookContentIDs": facebookContentIDs,
        "facebookProducts": facebookProducts,
        "content_type": "product",
        "value": parseFloat(products[0].price)
    });

    sessionStorage.clear()
  }
}

export default {
  name: 'Checkout',
  data () {
    return {
      card: "",
      stripe: "",
      firstname: "",
      lastname: "",
      email: "",
      password: "",
      stripe_token: "",
      subscription_plan: ""
    }
  },
  beforeCreate: function () {
    client_helper.setSEOData("Checkout - Tracing Delta", "Tracing Delta is chagning the game for retail traders by providing actionable stastics on the fundamental and technical aspects of the market.");
  },
  methods: {
    moveToAccountInfo: function () {
      var radios = document.getElementsByName('plan');
      for (var i = 0, length = radios.length; i < length; i++) {
        if (radios[i].checked){
          this.subscription_plan = radios[i].value;
          break;
        }
      }
      // Check if free account option is selected
      if(this.subscription_plan == "free") {
        // If so, swap buttons
        document.getElementById("paid-next").classList.toggle("hide")
        document.getElementById("free-next").classList.toggle("hide")
      }

      // Change views
      document.getElementsByClassName('checkout-box-content')[0].classList.toggle('hide')
      document.getElementsByClassName('checkout-box-content')[1].classList.toggle('hide')
      gtmCheckoutStepOne()
      gtmAddToCart()
      gtmCheckoutStepTwo()
    },
    moveBackToPlanInfo: function () {
      // Change views
      document.getElementsByClassName('checkout-box-content')[0].classList.toggle('hide')
      document.getElementsByClassName('checkout-box-content')[1].classList.toggle('hide')
    },
    moveToPaymentInfo: function () {
      // Validate Account Info
      let inputs_are_valid = validateAccountInfo()
      if (inputs_are_valid == false) return
      this.firstname = document.getElementById("firstname").value;
      this.lastname = document.getElementById("lastname").value;
      this.email = document.getElementById("email").value;
      this.password = document.getElementById("password").value;


      // Create a Stripe client.
      var stripe = Stripe(client_config.stripe_public_key)
      this.stripe = stripe
    
      // Create an instance of Elements.
      var elements = stripe.elements();
      var style = {
        base: {
          color: '#32325d',
          fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
          fontSmoothing: 'antialiased',
          fontSize: '16px',
          '::placeholder': {
            color: '#aab7c4'
          }
        },
        invalid: {
          color: '#fa755a',
          iconColor: '#fa755a'
        }
      };

      // Create an instance of the card Element.
      var card = elements.create('card', {style: style});
      this.card = card;

      // Add an instance of the card Element into the `card-element` <div>.
      card.mount('#card-element');

      // Change Views
      document.getElementsByClassName('checkout-box-content')[1].classList.toggle('hide')
      document.getElementsByClassName('checkout-box-content')[2].classList.toggle('hide')
      gtmCheckoutStepThree()
    },
    moveBackToAccountInfo: function () {
      // Change Views
      document.getElementsByClassName('checkout-box-content')[1].classList.toggle('hide')
      document.getElementsByClassName('checkout-box-content')[2].classList.toggle('hide')
    },
    completeRegistration: async function () {
      document.getElementById("account-info-errors").innerHTML = ""
      if (this.subscription_plan != "free") {
        const stripe = this.stripe
        // Create Stipe Token
        this.stripe_token = await stripe.createToken(this.card).then(function(result) {
          if (result.error) {
            // Inform the customer that there was an error.
            var errorElement = document.getElementById('card-errors');
            errorElement.textContent = result.error.message;
          } else {
            // Send the token to your server.
            return result.token
          }
        });

        // Send Data to Server
        let token = await axios.post(client_config.base_url + '/api/user/register', {
          firstname: this.firstname,
          lastname: this.lastname,
          email: this.email,
          password: this.password,
          stripe_token: this.stripe_token,
          subscription_plan: this.subscription_plan
        })
        .then(function (response) {
          if (response.data.success == false) {
            document.getElementById("account-info-errors").innerHTML = response.data.message
            document.getElementsByClassName('checkout-box-content')[1].classList.toggle('hide')
            document.getElementsByClassName('checkout-box-content')[2].classList.toggle('hide')
          } else {
            return response.data.token
          }
        })
        .catch(function (error) {
          console.log(error);
        });

        localStorage.setItem('token', token)
        this.$parent.authed = true

        // Show Checkout Complete/Thank You! Page
        document.getElementsByClassName('checkout-row')[0].classList.toggle('hide')
        document.getElementsByClassName('thank-you-row')[0].classList.toggle('hide')
        gtmCheckoutStepFour()
      } else if (this.subscription_plan == "free") {
        // Validate Inputs
        let inputs_are_valid = validateAccountInfo()
        if (inputs_are_valid == false) return
        this.firstname = document.getElementById("firstname").value;
        this.lastname = document.getElementById("lastname").value;
        this.email = document.getElementById("email").value;
        this.password = document.getElementById("password").value;

        // Send Data to Server
        let token = await axios.post(client_config.base_url + '/api/user/register', {
          firstname: this.firstname,
          lastname: this.lastname,
          email: this.email,
          password: this.password,
          stripe_token: this.stripe_token,
          subscription_plan: this.subscription_plan
        })
        .then(function (response) {
          if (response.data.success == false) {
            document.getElementById("account-info-errors").innerHTML = response.data.message
            return false
          } else {
            console.log(response.data)
            localStorage.setItem('token', response.data.token)
            return true
          }
        })
        .catch(function (error) {
          console.log(error);
        });

        localStorage.setItem('token', token)
        this.$parent.authed = true

        // Show Checkout Complete/Thank You! Page
        document.getElementsByClassName('checkout-row')[0].classList.toggle('hide')
        document.getElementsByClassName('thank-you-row')[0].classList.toggle('hide')
        gtmCheckoutStepThree()
        gtmCheckoutStepFour()
      }
    }
  }
}
</script>

<style scoped>
  .checkout-box, .thank-you-container {
    padding: 16px;
    border: 1px solid black;
    width: 100%;
    max-width: 450px;
    margin-bottom: 32px;
  }
  .thank-you-container {
    display: block;
    margin: auto;
  }

  .checkout-box-content {
    transition: max-height 0.2s ease-out;
  }
 #card-element {
   width: 100% !important;
   margin: 16px 0px 16px 0px;
 }
.StripeElement {
  box-sizing: border-box !important;
  height: 40px !important;
  padding: 10px 12px !important;
  border: 1px solid black !important;
  border-radius: 0px !important;
  background-color: white !important;
  box-shadow: 0 1px 3px 0 #e6ebf1 !important;
  -webkit-transition: box-shadow 150ms ease !important;
  transition: box-shadow 150ms ease !important;
}

.StripeElement--focus {
  box-shadow: 0 1px 3px 0 #cfd7df !important;
}

.StripeElement--invalid {
  border-color: #fa755a !important;
}

.StripeElement--webkit-autofill {
  background-color: #fefde5 !important;}
#card-element {
  margin: 0px 0px 32px 0px;
}

.features-table, .features-table thead, .features-table tbody {
  width: 100% !important;
}
.features-table thead {
  text-align: center !important;
  font-weight: 700 !important;
  color: #fff !important;
  background: #000 !important;
}

.features-table .table-col-one {
  width: 70% !important;
}
.features-table .table-col-two, .features-table .table-col-three {
  width: 15% !important;
}

.features-table td, .features-table th {
  padding: 8px !important;
  border: 1px solid black !important;
}
</style>



