<template>
  <div class="container">
    <div class="row">
      <div class="col-12">
        <table class="swing-data-table">
          <thead>
            <tr>
              <th style="border-right: 1px solid #fff;"></th>
              <th style="border-bottom: 1px solid #fff; border-right: 1px solid #fff;" colspan="4">Current Swing Data</th>
              <th style="border-bottom: 1px solid #fff; border-right: 1px solid #fff;"  colspan="6">Probability of Swing Making a New Extreme Before Retracing 200 Pips</th>
              <th style="border-bottom: 1px solid #fff; border-right: 1px solid #fff;" colspan="4">Probability of Swing Traveling X Pips</th>
              <th style="border-bottom: 1px solid #fff;" colspan="4">Probability of Swing Lasting X Candles</th>
            </tr>
            <tr>
              <th style="border-right: 1px solid #fff;">Pair</th>
              <th>Direction</th>
              <th>Run Up</th>
              <th>Length</th>
              <th style="border-right: 1px solid #fff;">Extremes</th>
              <th>0</th>
              <th>1</th>
              <th>2</th>
              <th>3</th>
              <th>4</th>
              <th style="border-right: 1px solid #fff;">5</th>
              <th>50%</th>
              <th>25%</th>
              <th>12.5%</th>
              <th style="border-right: 1px solid #fff;">6.25%</th>
              <th>50%</th>
              <th>25%</th>
              <th>12.5%</th>
              <th>6.25%</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="data in swing_data">
              <td>{{data.pair.replace("_","/")}} {{data.time_frame == 1440 ? "(D)" : "(W)"}}</td>
              <td>{{data.current_swing_direction == 1 ? "Up" : "Down"}}</td>
              <td>{{data.current_swing_high_pips.toFixed(0)}}</td>
              <td>{{data.current_swing_length}}</td>
              <td>{{data.current_swing_high_breaks}}</td>
              <td>{{(data.no_breaks * 100).toFixed(2) + "%"}}</td>
              <td>{{(data.one_breaks * 100).toFixed(2) + "%"}}</td>
              <td>{{(data.two_breaks * 100).toFixed(2) + "%"}}</td>
              <td>{{(data.three_breaks * 100).toFixed(2) + "%"}}</td>
              <td>{{(data.four_breaks * 100).toFixed(2) + "%"}}</td>
              <td>{{(data.five_breaks * 100).toFixed(2) + "%"}}</td>
              <td>{{data.run_up_cut_off_one}}</td>
              <td>{{data.run_up_cut_off_two}}</td>
              <td>{{data.run_up_cut_off_three}}</td>
              <td>{{data.run_up_cut_off_four}}</td>
              <td>{{data.length_cut_off_one}}</td>
              <td>{{data.length_cut_off_two}}</td>
              <td>{{data.length_cut_off_three}}</td>
              <td>{{data.length_cut_off_four}}</td>
            </tr>
          </tbody>
        </table>
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios'
import client_config from '../../client_config.js'
import client_helper from '../../client_helper.js'

export default {
  name: 'Dashboard',
  data () {
    return {
      swing_data: []
    }
  },
  beforeCreate: function () {
    client_helper.setSEOData("Dashboard - Tracing Delta", "Tracing Delta is chagning the game for retail traders by providing actionable stastics on the fundamental and technical aspects of the market.");
  },
  created: function () {
    this.fetchCurrencyData()
  },
  methods: {
    fetchCurrencyData: async function () {
      let data = await axios.get(client_config.base_url + "/api/dashboard/get-all-data", {
        headers: {'Authorization': "bearer " + localStorage.getItem('token')}
      })
      .then(function (response) {
        if (response.data.success == false) {
          return false
        } else {
          return response.data.return_data
        }
      })
      .catch(function (error) {
        console.log(error);
      });
      this.swing_data = data.swing_data
      if (this.$parent.paid_subscription == false) {
        this.swing_data = this.swing_data.filter(function (d) {
          return d.pair == "EUR_USD"
        })
      }
    },
    toggleRetailPairs: function (event) {
      let el = event.target;
      let retail_pairs_box = el.parentNode.parentNode.querySelector(".retail-sentiment-details-content")
      console.log(retail_pairs_box.classList)
      if (retail_pairs_box.classList.length > 1) {
        el.innerHTML = "Hide Pairs"
      } else {
        el.innerHTML = "Show Pairs"
      }
      retail_pairs_box.classList.toggle("hide")
    }
  },
}
</script>

<style scoped>
  .currency-container {
    border: 1px solid #000;
    padding: 16px;
    margin-bottom: 32px;
  }

  .professional-sentiment, .retail-sentiment, .economic-data {
    border: 1px solid #000;
    padding: 32px;
    margin-bottom: 32px;
  }

  .sentiment-data-container {
    width: 100%;
    height: 25px;
    border: 1px solid #000;
    margin-bottom: 16px;
  }
  .sentiment-data-container .long-percentage {
    width: 50%;
    background: #000;
    height: 23px;
    display: inline-block;
  }
  .sentiment-data-container .short-percentage {
    width: 50%;
    background: #fff;
    height: 23px;
    display: inline-block;
  }

  .sentiment-label-container {
    display: flex;
    width: 100%;
  }
  .sentiment-label-container .long-label {
    width: 50%;
    display: inline-block;
    float: left;
  }
  .sentiment-label-container .short-label {
    width: 50%;
    display: inline-block;
    float: right;
  }

  .sentiment-label-container .long-label p, .sentiment-label-container .short-label p  {
    text-align: center;
    width: 50px;
  }
  .sentiment-label-container .short-label p {
    float: right;
  }

  .swing-data-table, .swing-data-table thead, .swing-data-table tbody {
    width: 100%;
  }

  .swing-data-table {
    text-align: center;
  }

  .swing-data-table thead {
    font-weight: 700;
    color: #fff;
    background: #000;
    padding: 8px;
    border: 1px solid #000;
  }

  .swing-data-table tbody {
    font-size: 12px;
    border: 1px solid #000;
  }

  .swing-data-table td, .swing-data-table th {
    border: 1px solid #000;
  }

  .swing-data-table td {
    padding: 4px;
    font-size: 12px !important;
  }

  .swing-data-table th {
    padding: 8px;
    font-size: 12px !important;
  }
</style>



